import React from 'react';
import { useLocation, useParams } from 'react-router-dom';
import { MonsterCard } from '../components/MonsterCard';
import { library } from '../models/Library';
import { Monsters } from '../models/Models';

interface MonsterScreenProps {
  monster: Monsters
}

type QuizParams = {
  tree: string;
  monster: string;
};

function useQuery() {
  const { search } = useLocation();
  console.log(search)
  console.log(new URLSearchParams(search))
  return new URLSearchParams(search)
}

export const MonsterScreen = (props: MonsterScreenProps | any) => {
  let mon = props.monster;
  if (mon === undefined) {
    mon = props.match.params.monster;
  }
  let monster = library.get(mon);
  
  let m = useParams<QuizParams>();

  console.log(m)
  let query = useQuery();
  console.log(query.get('tree'))
  return (
    <MonsterCard monster={monster} />
  );
}
