export class TreeNode<Type> {
    data: Type
    left: TreeNode<Type> | undefined
    right: TreeNode<Type> | undefined

    constructor(d: Type){
        this.data = d;
    }

    toList(): Type[]{
        if(this.left && this.right){
            return [this.data].concat(this.left.toList()).concat(this.right.toList())
        }
        else if(this.left){
            return [this.data].concat(this.left.toList())
        }
        else if(this.right){
            return [this.data].concat(this.right.toList())
        }
        else return [this.data];
    }

    // static fromList(l: any[]): TreeNode<any> {
        
    //     const root = new TreeNode(l[0]);

    //     if(l[0] !== -1){
    //         root.left = this.fromList(l.slice(1))
    //     }
    // }
}

